* {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  box-sizing: border-box;
  font-size: 16px;
  text-decoration: none;
  color: inherit;
  font-size: inherit;
}

#root {
  left: 0;
  height: 100%;
  overflow: scroll;
  position: fixed;
  top: 0;
  width: 100%;
}

#app {
  height: 400%;
  width: 100%;
  position: relative;
  top: 0%;
  left: 0%;
  transition: all 300ms ease-in-out; }
  #app .section {
    height: 25%; }

.menu {
  position: fixed;
  top: -100%;
  left: 0;
  height: 100vh;
  width: 100%;
  transition: all 500ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
  background-image: linear-gradient(to right, #072142, #8c2b7a 42%, #ff4d5a);
  z-index: 0;
  text-align: center;
  padding-top: 2rem; }
  .menu.active {
    top: 0;
    z-index: 2; }
  .menu ul {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    width: 80%;
    height: 100%; }
    .menu ul li {
      cursor: pointer;
      color: #FFFFFF;
      font-size: 2rem;
      font-family: "Comfortaa", cursive;
      line-height: 7rem;
      transition: all 300ms ease-in-out; }
      .menu ul li:hover {
        font-size: 2.5rem;
        font-weight: bold; }

.cross {
  cursor: pointer;
  position: fixed;
  top: 0;
  right: 0;
  width: 50px;
  height: 30px;
  margin: 10px;
  z-index: 3; }
  .cross .cross-line {
    display: block;
    background: #FF4D5A;
    margin-top: 5px;
    height: 5px;
    width: 100%;
    opacity: 1;
    transition: all 300ms ease-in-out; }
  .cross.active .cross-line {
    background: #FFFFFF; }
  .cross.active .cross-line.one {
    opacity: 0; }
  .cross.active .cross-line.two {
    transform: rotate(45deg);
    position: relative;
    top: 20px; }
  .cross.active .cross-line.three {
    transform: rotate(-45deg);
    position: relative;
    top: 10px; }

.bullets {
  position: fixed;
  height: 100px;
  right: 0;
  top: calc(50% - 50px);
  margin: 10px; }
  .bullets .bullet {
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.1);
    cursor: pointer;
    display: block;
    border-radius: 30px;
    width: 15px;
    height: 15px;
    margin: 10px;
    background: #C0C5CA; }
    .bullets .bullet.active {
      background: #072142; }

